.btn {
  position: relative;
  display: inline-block;
  padding: 16px 42px;
  outline: 0;
  font-family: "Ubuntu-Medium", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  border-radius: 28px;
  cursor: pointer;
  transition: .1s ease-in-out;
}

.btn::after, .btn::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 28px;
  content: '';
}

.btn span {
  position: relative;
  z-index: 1;
}

.btn_red {
  color: #fff;
}

.btn_red:hover::before {
  background-color: #ff0315;
}

.btn_red:active {
  transform: translateY(5px);
}

.btn_red:active::after {
  box-shadow: none;
  transform: translateY(0);
}

.btn_red::after {
  transform: translateY(5px);
  background-color: #9c1019;
  box-shadow: 0 0 48px 3px rgba(240, 67, 79, 0.2);
  transition: .1s ease-in-out;
}

.btn_red::before {
